header {
	background-color: gray;
	height: 100px;
}
.header-container {
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	padding: 0px 50px;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
}
.logo a {
	margin: 0px;
	color: red;
	font-weight: bold;
	font-size: 40px;
}
.logo a:hover, .logo a:focus {
	text-decoration: none;
}
.filtro ul {
	margin: 0px;
	padding: 0px;
}
.filtro ul li {
	list-style: none;
	display: inline-block;
	margin: 0px 30px;
}
.filtro a {
	font-size: 18px;
	color: white;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
}
.filtro a:hover {
	text-decoration: none;
	color: red;
}
.filtro a:focus {
	outline: 0;
	text-decoration: none;
}


