.catalog-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 60px 0px;
}
.catalog-search {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 60px 0px 100px;
}
.catalog-search__wrapper {
	width: 50%;
	position: relative;
}
.catalog-search__wrapper input {
	width: 100%;
	padding: 10px 0px;
	background: none;
	border: 0px;
	border-bottom: 1px solid gray;
	outline: 0;
	font-size: 18px;
	color: white;
}
::-webkit-input-placeholder {
	color: gray;
	font-style: italic;
}
.border-focus {
	position: absolute;
	bottom: 0px;
	width: 0px;
	border-bottom: 1px solid red;
	-webkit-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
}
.catalog-search__wrapper input:focus + .border-focus {
	width: 100%;
}
.find-null {
	margin-top: 60px;
	font-size: 40px;
	color: gray;
	text-align: center;
}
.find-null i {
	font-size: 60px;
	margin-bottom: 20px;
}


